<template>
  <div id="duck">
    <transition name="rotate">
      <img
        src="~assets/img/duck.png"
        alt="a little duck"
        :class="direction"
        :style="style"
        @click="handleClick"
        v-show="show"
      />
    </transition>
  </div>
</template>

<script>
  import { commitKill } from "network/meta";

  let dir2num = { left: 1, right: -1 };

  export default {
    name: "Duck",
    components: {},
    data() {
      return {
        wanderTimmer: null,
        direction: "left",
        clientX: 10,
        clientY: 10,
        show: true,
        alive: true,
        wanderTime: 700
      };
    },
    computed: {
      style() {
        return {
          top: this.clientY + "px",
          left: this.clientX + "px"
        };
      }
    },
    watch: {
      clientX: function(newX, oldX) {
        if ((newX - oldX) * dir2num[this.direction] < 0) {
          this.direction = this.direction == "left" ? "right" : "left";
        }
      },
      "$store.state.duckAlive": function(newState, oldState) {
        if (!newState) {
          this.direction = "hit";
          clearInterval(this.wanderTimmer);
          setTimeout(() => {
            this.show = false;
          }, 500);
        } else {
          this.show = true;
          this.direction = "left";
          setTimeout(() => {
            this.wander();
          }, 20);
          this.wanderTimmer = setInterval(this.wander, this.wanderTime);
        }
        this.alive = !this.alive;
      }
    },
    methods: {
      wander() {
        this.clientY =
          Math.random() * (document.documentElement.clientHeight - 20);
        this.clientX =
          Math.random() * (document.documentElement.clientWidth - 20);
      },
      handleClick() {
        let data = sessionStorage.getItem("duckKilled");
        if (!data) {
          sessionStorage.setItem("duckKilled", 1);
          alert("‰Ω†Êäì‰Ωè‰∫ÜÂ∞èÈªÑÈ∏≠!");
          alert(
            "Êï∞ÊçÆÂ∑≤ÁªèÊèê‰∫§, Áî®ÁîµËÑëÊâìÂºÄÂçöÂÆ¢, Âà∑Êñ∞ÂêéÂèØ‰ª•ÁúãÂà∞Â∞èÈªÑÈ∏≠‰∏ÄÂÖ±Ë¢´Êäì‰Ωè‰∫ÜÂ§öÂ∞ëÊ¨°!"
          );
          data = 0;
        }
        if (data > 9) {
          alert("ÊàëÂøç‰Ω†Âæà‰πÖ‰∫ÜÂïä");
          alert("ÁßòÊäÄ: Â∞ÅÂç∞!");
          alert("‰Ω†Áé∞Âú®Âà´ÊÉ≥ÂíåÂ∞èÈªÑÈ∏≠Áé©ËÄç‰∫Ü!!");
          return;
        }
        sessionStorage.setItem("duckKilled", +data + 1);
        if (data == 2) {
          alert("ÂñÇ, ‰Ω†ÊòØ‰∏çÊòØËøá‰∫éÊÆãÂøç‰∫Ü??");
          alert("Âì¶, ÊàëÂ§™‰∫ÜËß£‰Ω†‰∫Ü, ");
          alert("‰Ω†ÊùÄ‰∫ÜËøô‰πàÂ§öÂ∞èÈªÑÈ∏≠Âè™ÊòØ‰∏∫‰∫ÜÊääËÆ°ÂàÜ‰∏äÁöÑÊ¨°Êï∞Êä¨È´ò‰∏çÊòØÂêó???");
          alert("Â•Ω, ‰Ω†ÂñúÊ¨¢Áé©");
          alert("Â∞èÈªÑÈ∏≠ÁîüÊ∞î‰∫Ü!!");
          alert("Á≠âÁùÄÁûßÂêß!!");
          this.wanderTime = 400;
        } else if (data >= 5) {
          alert("Â•ΩÂ•ΩÂ•Ω");
          alert("‰Ω†Ëµ¢‰∫Ü");
          alert("Ê±Ç‰Ω†ÊîæËøáÂ∞èÈªÑÈ∏≠ÂêßQwQ");
        }
        commitKill();
        this.$store.commit("toggleDuck");
      }
    },
    created() {},
    mounted() {
      if (sessionStorage.getItem("duckKilled") >= 2) {
        this.wanderTime = 400;
      }
      this.wander();
      this.wanderTimmer = setInterval(this.wander, this.wanderTime);
    }
  };
</script>
<style scoped>
  #duck img {
    width: 1.8rem;
    height: 1.8rem;
    transition: all 0.7s ease;
    cursor: crosshair;
    position: fixed;
    z-index: 7;
  }
  .right {
    transform: rotateY(180deg);
  }
  .hit {
    transform: rotateY(700deg);
    opacity: 0;
  }
</style>